.image-container {
  position: relative;
  width: 100%;
  height: 42em;
  overflow: hidden;
  object-fit: contain;
}
.moving-image {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%); /* Center the image horizontally */
  width: 100%;
  height: auto; /* Ensure the image height matches the container height */

}

.banner-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}
.banner-text h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}
.banner-text button {
  padding: 10px 20px;
  font-size: 1rem;
  margin: 5px;
  cursor: pointer;
  border: none;
  background-color:  #f277c6;
  color: white;
  border-radius: 5px;
}

.center-text {
  font-size: 1.2em;
  text-align: center; /* Centers the text horizontally */
  margin: 0 auto; /* Ensures the element itself is centered */
  display: block; /* Makes sure the element is treated as a block-level element */
  padding: 10px; /* Optional: Adds some space around the text */
  background-color:#f277c77e;

}
/* Container for the background image */
.background-container {
  position: relative;
  height: 60vh; /* Full height of the screen */
  background-image: url('../public/b2.jpg'); /* Replace with your image path */
  background-size: cover;
  background-attachment: fixed; /* Makes the background scroll with content */
  background-position: center;
  overflow: hidden;
}

/* Overlay that holds the icons and text */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
  text-align: center;
}

/* Container for the icons and text */
.icon-text-container {
  display: flex;
  flex-wrap: wrap; /* Allow wrapping to the next line */
  justify-content: center; /* Center the items */
  gap: 20px; /* Space between items */
  padding: 20px;
}

/* Icon and text style */
.icon-text {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white; /* Text color white */
  font-size: 16px; /* Adjust font size for better readability */
  gap: 10px;

  text-align: center;
}

.icon {
  width: 50px; /* Icon size */
  height: 50px;
}

.text {
  font-weight: bold;
  max-width: 150px; /* Limit text width to keep it neat */
  word-wrap: break-word; /* Ensure text wraps if it's too long */
}

/* Mobile responsiveness: Stacks items vertically */
@media (max-width: 768px) {
  .icon-text-container {
    flex-direction: column;
    gap: 15px;
  }

  .icon-text {
    width: 100%; /* Full width on smaller screens */
    justify-content: center;
  }

  .text {
    font-size: 14px; /* Adjust font size for smaller screens */
  }
}

